.reviews__block {
  width: 90%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (min-width: 768px) {
  .reviews__block {
    width: 793px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}