.footer__contacts {
  width: 100%;
  padding: 20px 0;
  border-bottom: 2px solid #ffffff;
  border-top: 2px solid #ffffff;
  visibility: hidden;
}

@media screen and (min-width: 768px) {
  .footer__contacts {
    width: 186px;
    border: none;
    padding: 0;
    visibility: visible;
  }
}